.ui.slider {
  line-height: 1;
  position: relative; }
  .ui.slider .slider-spinner {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center; }
  .ui.slider .slider-progress {
    position: absolute;
    height: 3px;
    width: 100%; }
    .ui.slider .slider-progress-bar {
      transition: all 2s;
      background-color: lightgrey;
      height: 3px;
      position: relative; }
    .ui.slider .slider-progress.top {
      top: -6px; }
    .ui.slider .slider-progress.bottom {
      bottom: -6px; }
  .ui.slider .slider-dots {
    width: 100%;
    text-align: center;
    padding: 10px 0; }
    .ui.slider .slider-dots .dot {
      display: inline-block;
      margin: 0px 3px;
      width: 10px;
      height: 10px;
      border-radius: 5px;
      background-color: lightgrey;
      cursor: pointer; }
      .ui.slider .slider-dots .dot.red {
        cursor: inherit;
        background-color: grey; }
  .ui.slider .slider-content {
    overflow: hidden;
    line-height: 1;
    width: 100%;
    height: 100%;
    position: relative; }
    .ui.slider .slider-content .slider-items {
      position: absolute;
      transition: all 2s;
      display: flex; }
      .ui.slider .slider-content .slider-items.hide {
        opacity: 0; }
      .ui.slider .slider-content .slider-items .slider-item {
        user-select: none;
        line-height: 1; }
        .ui.slider .slider-content .slider-items .slider-item img {
          width: auto;
          opacity: 1;
          transition: opacity 0.3s; }
          .ui.slider .slider-content .slider-items .slider-item img.hide {
            opacity: 0; }
  .ui.slider .slider-button {
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.8);
    opacity: 0;
    color: white;
    height: 100%;
    width: 50px;
    font-size: 3em;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute; }
    .ui.slider .slider-button.visible:hover {
      cursor: pointer; }
    .ui.slider .slider-button-left {
      left: 0; }
    .ui.slider .slider-button-right {
      right: 0; }
    .ui.slider .slider-button-bottom-left {
      left: 0;
      bottom: 0px;
      height: 50px;
      width: 50%; }
    .ui.slider .slider-button-bottom-right {
      right: 0;
      bottom: 0px;
      height: 50px;
      width: 50%; }
    .ui.slider .slider-button-top-left {
      left: 0;
      top: 0px;
      height: 50px;
      width: 50%; }
    .ui.slider .slider-button-top-right {
      right: 0;
      top: 0px;
      height: 50px;
      width: 50%; }
  .ui.slider:hover .slider-button.visible {
    opacity: 0.8; }

.fade-enter-active, .fade-leave-active {
  transition: opacity 1.5s; }

.fade-enter, .fade-leave-to {
  opacity: 0; }

[v-cloak] {
  display: none; }

.spinner {
  width: 70px;
  text-align: center; }

.spinner > div {
  width: 18px;
  height: 18px;
  background-color: #333;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both; }

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s; }

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s; }

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0); }
  40% {
    -webkit-transform: scale(1); } }

@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0); }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1); } }



